<template>
	<div>
		<HeaderTop>小莱电影</HeaderTop>
		<HeaderTitle>影片</HeaderTitle>
		<div class="container">
			<div class="movie-list" v-for="item in movieList">
				<router-link 
				:to="{
					path:'/detail',
					query:{
						id:item.MovieID,
						name:item.Name,
						img:item.ImgUrl,
						rate:item.Rate,
						actors:item.Actors,
						type:item.Type,
						releaseTime:item.ReleaseTime,
						intro:item.Intro
						}
					}">
					<div class="movie-img"><img :src="imgUrl+item.ImgUrl"/></div>
					<div class="movie-info">
						<div class="hd">
							<h2>{{item.Name}}</h2>
							<div class="movie-type"><span>2D</span><span>IMAX</span></div>
						</div>
						<p>{{item.Actors}}</p>
						<p>{{item.Type}}</p>
					</div>
					<div class="grade">
						{{item.Rate}}分
					</div>
					<div class="buy">
						<router-link 
						:to="{
							path:'/cinema',
							query:{
								id:item.MovieID,
								name:item.Name,
								rate:item.Rate
								}
						}">
						购票</router-link>
					</div>
				</router-link>
			</div>
		</div>
	</div>
</template>
<script>
import HeaderTop from '@/components/common/HeaderTop'
import HeaderTitle from '@/components/common/HeaderTitle'
export default {
  components: {
	  HeaderTop,
      HeaderTitle
  },
  data () {
	  return {
		 movieList:[],
		 isShow:false 
	  }
  },
  mounted () {
	  this.getData()
  },
  methods: {
	  getData(){
		  this.$http.get('/Movie/New')
		  .then((res)=>{
			  this.movieList=res.data
		  })
		  .catch((err)=>{
			  console.log(err)
		  })
	  }
  }
}
</script>




