<template>
  <div>
    <HeaderTop>
        <span class="go-back"><router-link to="home"></router-link></span>
        {{ $route.query.name}}
    </HeaderTop>
    <div class="container detail-container">
    	<div class="banner">
    		<div class="filter-box"></div>
    		<div class="thumbnail">
    			<img :src="imgUrl + $route.query.img" />
    		</div>
    		<div class="movie-intr">
    			<h1>{{$route.query.name}}</h1>
    			<div class="star-level">
    				<div id="star_con" class="star-vote">
						<span id="add_star" class="add-star"></span>
						<span id="del_star" class="del-star"></span>
					</div>
    				<div class="point">
    					{{$route.query.rate}}
    				</div>
    			</div>
    			<p>{{$route.query.type}}</p>
    			<p>{{$route.query.actors}}</p>
    			<p>上映时间：{{$route.query.releaseTime}}</p>
    		</div>
    	</div>
    	<div class="movie-recommend">
    		<p>{{$route.query.intro}}</p>
    	</div>
   	</div>
   <div class="order-bar">
			<router-link 
			:to="{
				path:'/cinema',
					query:{
					id:this.MovieId,
					name:this.MovieName,
					rate:this.MovieRate
				}
			}">
			立即购票</router-link>
   </div>
  </div>
</template>
<script>
import HeaderTop from '@/components/common/HeaderTop'
export default {
  components: {
      HeaderTop
	},
	data () {
		return {
			MovieId:this.$route.query.id,
			MovieName:this.$route.query.name,
			MovieRate:this.$route.query.rate,
		}
	},
  mounted () {
       this.showStar(this.$route.query.rate)
  },
  methods: {
    showStar(n){
        var con_wid=document.getElementById("star_con").offsetWidth;
        var del_star=document.getElementById("del_star");
        //透明星星移动的像素
        var del_move=(n*con_wid)/10;
        del_star.style.backgroundPosition=-del_move+"px 0px";
        del_star.style.left=del_move+"px";
    }
  }
}
</script>




