import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/pages/Index'
import Home from '@/components/pages/Home'
import HomeMain from '@/components/views/HomeMain'
import OrderMain from '@/components/views/OrderMain'
import UserMain from '@/components/views/UserMain'
import Detail from '@/components/pages/Detail'
import Cinema from '@/components/pages/Cinema'
import Seat from '@/components/pages/Seat'
import Login from '@/components/pages/Login'
import Register from '@/components/pages/Register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Index
    }, 
    {
      path: '/home',
      component: Home,
      redirect: '/home/main',
      children:[
        { path: 'main', component: HomeMain},
        { path: 'order', component: OrderMain},
        { path: 'user', component: UserMain}
      ]
    },
    {
      path: '/detail',
      component: Detail
    },
    {
      path: '/cinema',
      component: Cinema
    },
    {
       path: '/seat',
       component: Seat
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/register',
      component: Register
    },
    {
       path: '*',
       redirect: 'home'
    }
  ]
})



