<template>
  <div>
      <HeaderTop>
    <span class="go-back"><router-link to="home"></router-link></span>
    影院
    </HeaderTop>
    <div class="seat-title">
        <h2>{{ $route.query.movieName }}</h2>
        <h4>
            今天{{(new Date().getMonth() + 1) + "月" + (new Date().getDate()) + "日 " }} 
            {{ShowsDetail.Time}} 国语2D
        </h4>
        <div class="lobby">
        	<span>{{ShowsDetail.Room}}银幕</span>
        </div>
    </div>
    <div class="container seat">
		<div class="seating">
			<ul>
				<li v-for="seat in seatlist" :class="seat.status" @click="setSeat(seat.id)"></li>
			</ul>
		</div>
		<div class="tips-seat">
			<span class="selecting">可选</span><span class="selected">已选</span><span class="selled">已售</span>
		</div>
		<div class="ticket">
			<h5>已选座位</h5>
			<div class="ticket-item" v-for="(item,index) in ticketArray">
				<a href="javascript:void(0)" @click="delTick(index)">
					{{ticketArray[index].Row}}排{{ticketArray[index].Col}}座<em>￥{{ShowsDetail.Price}}</em>
				</a>
			</div>
		</div>
   </div>
    <div class="seat-bar" @click="buy">
   		<a href="#">{{totalPrice}}确认选座</a>
   	</div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui'
import HeaderTop from '@/components/common/HeaderTop'
export default {
    components: {
	  HeaderTop,
		},
		 data () {
        return {
						seatlist:[],
						ShowsDetail:[],
						TimeID:this.$route.query.TimeID,
						ticketArray:[]
        }
		},
		created () {
			for(var i=0;i<100;i++){
					var item = {id:i,status:""};
					this.seatlist[i] = item;
			};
			this.fetchData()
		},
		methods: {
        fetchData(){
          // 加载当前影片具体时间、银幕
 					this.$http.get('Movie/ShowsDetail?TimeID='+ this.TimeID)
            .then(res=>{
                this.ShowsDetail=res.data
            })
            .catch(err=>{
                console.log(err)
                Toast({
                message: '网络错误，不能访问',
                position: 'top',
                duration: 3000
                });
                return
						});
					// 加载已购买座位
          this.$http.get("/Movie/Seat?TimeID="+ this.TimeID,{
                headers: {"authorization": "basic "+localStorage.tokenID}
                })
						.then(res => {
							for (var i = 0; i < res.data.length; i++) {
											var index = (res.data[i].Row - 1) * 10 + res.data[i].Col - 1;
											this.seatlist.push(index);
											this.seatlist[index].status="active-selled";
										}
						})
						.catch(err => {
							console.log(err);
						})
		},
		setSeat(index){
			if (this.ticketArray.length>=4) {
                Toast({
                message: '最多购买4张',
                position: 'middle',
                duration: 3000
                })
                return;
			} else {
				if(this.seatlist[index].status=="")
				{
					this.seatlist[index].status="active";
					var row = Math.floor(index / 10) + 1;
					var col = index % 10+1;
					this.ticketArray.push({Row:row,Col:col})
				}
			}
		},
		delTick(index){
			var row = this.ticketArray[index].Row;
			var col = this.ticketArray[index].Col;
			this.ticketArray.splice(index, 1)
			var index2 = (row - 1) * 10 + col - 1;
			this.seatlist[index2].status="";
		},
		buy(){
				if(this.ticketArray.length>0){
						//购买座位
						this.$http.post("/Orders",this.qs.stringify({
								"TimeID":this.TimeID,
								"SeatList":this.ticketArray
						}),{headers: {"authorization": "basic "+localStorage.tokenID}})
						.then(res => {
								Toast({
										message: '下单成功',
										position: 'middle',
										duration: 3000
										})
								this.$router.push({ path: "/order"});
						})
						.catch(err => {
								console.log(err);
						});
				}
				else{
						Toast({
						message: '你还没有选择座位',
						position: 'middle',
						duration: 3000
						})
						return;
				}
		}
	},
	computed: {
    totalPrice: function () {
			var total = this.ticketArray.length * this.ShowsDetail.Price;
			if(total)
				return "￥"+total;
		}
	}
}
</script>


