<template>
    <div class="nav-bar">
   		<ul>
   			<li><router-link :to="{path:'/home/main'}" active-class="active01"><span>电影</span></router-link></li>
   			<li><router-link :to="{path:'/home/order'}" @click.native="isTokenID" active-class="active02"><span>订单</span></router-link></li>
   			<li><router-link :to="{path:'/home/user'}" @click.native="isTokenID" active-class="active03"><span>我的</span></router-link></li>
   		</ul>
    </div>
</template>
<script>
import { Toast } from 'mint-ui'
export default {
  methods: {
	  isTokenID(){
		 if (!localStorage.tokenID) {
			 Toast({
                message: '尚未登录',
                position: 'middle',
                duration: 2000
                });
                this.$router.push("Home");
		 }
	  }
  }
}
</script>


