<template>
  <div>
    <HeaderTop>
        <span class="go-back"><router-link to="home"></router-link></span>
        影院
        </HeaderTop>
    <HeaderTitle>{{ $route.query.name}}<em>{{ $route.query.rate}}分</em></HeaderTitle>
    <div class="container">
		<div class="scene-item" v-for="item in movieShow">
			<span class="c-time">{{item.Time.substring(0, 5)}}</span>
			<span class="c-type">国语3D<em>{{item.Room}}</em></span>
			<span class="c-price">￥{{item.Price}}</span>
			<span class="c-btn">
        <button @click="selectTick(item.TimeID)">选择购票</button>
      </span>
		</div>
   </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui'
import HeaderTop from '@/components/common/HeaderTop'
import HeaderTitle from '@/components/common/HeaderTitle'
export default {
    components: {
	  HeaderTop,
      HeaderTitle
  },
  data () {
      return {
          movieName:this.$route.query.name,
          movieShow:[]
      }
  },
  mounted () {
        this.fetchData()
  },
  methods: {
      fetchData(){
					if (!localStorage.tokenID) {
					Toast({
					message: '尚未登录',
					position: 'middle',
					duration: 2000
					});
					this.$router.push("login")
					return
			}
 			this.$http.get('Movie/Shows?MovieID='+ this.$route.query.id)
            .then(res=>{
                this.movieShow=res.data
            })
            .catch(err=>{
              console.log(err.response.status)
            })
        },
        selectTick(TimeID){
            this.$router.push({path:'/seat',query:{TimeID:TimeID,movieName:this.movieName}})
        }
  }
}
</script>


