<template>
  <div>
    <HeaderTop>
        <span class="go-back"><router-link to="home"></router-link></span>
        <span class="go-next"><router-link to="login">登录</router-link></span>
        用户注册
    </HeaderTop>
    <div class="reg-main">
    	<div class="reg-from">
    		<label><input type="text" placeholder="请输入用户名" v-model="username"/></label>
    		<label><input type="password" placeholder="请输入密码" v-model="password"/></label>
    		<label><input type="password" placeholder="请确认密码" v-model="password2"/></label>
    	</div>
    	<div class="treaty">
    		<input type="checkbox" id="treaty" v-model="checked"/>
    		<label for="treaty">我已阅读并同意《格莱科技用户协议》</label>
    	</div>
    	<div class="login-btn reg-btn">
			<a href="#" @click="register">立即注册</a>
		</div>
		<div class="contact">
			<span>© 格莱科技 027-87229009</span>
		</div>
    </div>
  </div>
</template>
<script>
import HeaderTop from '@/components/common/HeaderTop'
import { Toast } from 'mint-ui'
export default {
  components: {
      HeaderTop,
  },
  data () {
      return {
          username:'',
          password:'',
          password2:'',
          checked:true
      }
  },
  methods: {
      register(){
          if (this.username=='') {
            Toast({
                message: '用户名不能为空',
                position: 'top',
                duration: 3000
                });
                return
          }
          if (this.password=='') {
              Toast({
                message: '密码不能为空',
                position: 'top',
                duration: 3000
                });
                return
          }
          if (this.password!=this.password2) {
               Toast({
                message: '两次密码不一致',
                position: 'top',
                duration: 3000
                });
              return
          }
          if (!this.checked) {
              Toast({
                message: '请同意协议',
                position: 'top',
                duration: 3000
                });
                return
          }
        this.$http.post('/Register',this.qs.stringify({
                  UserName:this.username,
                  Password:this.password
        }))
        .then((res)=>{
            console.log(res)
            Toast({
                message: '注册成功！',
                position: 'top',
                duration: 3000
                });
            this.$router.push('login')
        })
        .catch((err)=>{
           if (err.response.status==409) {
                  Toast({
                    message: '该用户已经注册',
                    position: 'top',
                    duration: 3000
                    });
                  return
              }
              if (err.response.status==404) {
                  Toast({
                    message: '网路请求失败，请稍候再试',
                    position: 'top',
                    duration: 3000
                    });
                  return
              }
              console.log(err.response.status)
        })
      }
  }
}
</script>



