<template>
    <main class="login-main">
		<div class="login-logo"><img src="static/img/LOGO.png"/></div>
		<div class="login-from">
			<label><input type="text" placeholder="请输入用户名" v-model="username"/></label>
			<label><input type="password"  placeholder="请输入密码" v-model="password"/></label>
		</div>
		<div class="login-btn">
			<a href="#" @click="loginPost">登录</a>
		</div>
		<div class="link-reg">
			<router-link to="register">还没账号？</router-link>
		</div>
		<div class="link-home">
			<router-link to="home">暂不登录</router-link>
		</div>
	</main>
</template>
<script>
import { Toast } from 'mint-ui'
export default {
  data () {
      return {
          username:'',
          password:''
      }
  },
  methods: {
      loginPost(){
          if (this.username=='') {
              Toast({
                message: '用户名不能为空',
                position: 'top',
                duration: 3000
                });
                return
            }
          else if(this.password==''){
                Toast({
                    message: '密码不能为空',
                    position: 'top',
                    duration: 3000
                    });
                return
          }
          this.$http.post("/Login",
          this.qs.stringify({
            UserName: this.username,
            Password: this.password
          })
        )
          .then((res)=>{
                localStorage.setItem('tokenID',res.data)
                Toast({
                    message: '登录成功',
                    position: 'top',
                    duration: 3000,
                });
                this.$router.push("Home");
          })
          .catch((err)=>{
              if (err.response.status==401) {
                  Toast({
                    message: '无法访问',
                    position: 'top',
                    duration: 3000
                    });
                  return
              }
              if (err.response.status==404) {
                  Toast({
                    message: '网路请求失败，请稍候再试',
                    position: 'top',
                    duration: 3000
                    });
                  return
              }
              console.log(err.response.status)
          });
      }
  }
}
</script>


