<template>
  <div>
      <HeaderTop>我的</HeaderTop>
      <div class="user-main">
    	<div class="user-item">
    		<a href="#">
    			<em>我的订单</em><span class="left_gray"></span>
    		</a>
    	</div>
    	<div class="user-item">
    		<a href="#">
    			<em>优惠卷</em><span class="left_gray"></span>
    		</a>
    	</div>
    	<div class="user-item">
    		<a href="#">
    			<em>影院会员卡</em><span class="left_gray"></span>
    		</a>
    	</div>
    	<div class="user-item">
    		<a href="#">
    			<em>想看的电影</em><span class="left_gray"></span>
    		</a>
    	</div>
    	<div class="user-item">
    		<a href="#">
    			<em>看过的电影</em><span class="left_gray"></span>
    		</a>
    	</div>
    <div class="login-btn user-btn">
			<a href="#" @click="exit">点击退出</a>
		</div>
		<div class="user-footer">
			<div class="user-logo"><img src="static/img/LOGO_gray.png"/></div>
			<p>客服电话：027-87229009</p>
		</div>
   </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui'
import HeaderTop from '@/components/common/HeaderTop'
export default {
  components: {
    HeaderTop
	},
	methods: {
		exit(){
			localStorage.removeItem('tokenID');
			Toast({
				message: '退出成功！',
				position: 'top',
				duration: 3000
			});
			this.$router.push("Home");
		}
	}
}
</script>



