<template>
  <div>
     <HeaderTop>我的订单</HeaderTop>
      <div class="container">
    	<div class="order-item" v-for="order in ordersList">
    		<dl>
    			<dt><img :src="imgUrl + order.ImgUrl"/></dt>
    			<dd><em>{{order.MovieName}} {{order.Total}}张</em></dd>
    			<dd>{{order.ReserveTime}}</dd>
    			<dd>{{order.Room}} {{order.Seat}}</dd>
    		</dl>
    		<div class="status">
    			<em>总价：{{order.Price}}元</em><span>已完成</span>
    		</div>
    	</div>
   </div>
  </div>
</template>
<script>
import HeaderTop from '@/components/common/HeaderTop'
export default {
  components: {
      HeaderTop
  },
  data () {
      return {
          ordersList:[]
      }
  },
  mounted () {
    this.getData()  
  },
  methods: {
      getData(){
					this.$http.get("/Orders",
					{headers: {"authorization": "basic "+localStorage.tokenID}})
			.then(res => {
                this.ordersList = res.data;
			})
			.catch(err => {
				console.log(err);
			})
      }
  }
}
</script>


